import React, { useState,useEffect } from 'react';
import axios from 'axios';
import { useNavigate,useLocation } from 'react-router-dom';
import OTPScreen from './OTP';
import loginBg from '../Images/bg.png';
import numbg from '../Images/numbg.png';

const Signup = () => {
  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [refers,setRefer]=useState('');
  const [error, setError] = useState('');
  const navigate = useNavigate();
  const location = useLocation();
  const phone = location.state ? location.state.phone : '';
  const refer=location.state?location.state.refer:'';
  const [versionLink, setVersionLink] = useState('');

  useEffect(() => {
    getVersionLink();
  }, []);

  const getVersionLink = async () => {
    try {
      const response = await fetch('https://sattajodileak.com/user/getVersion');
      const data = await response.json();
      if (data && data.latestEntry && data.latestEntry.link) {
        setVersionLink(data.latestEntry.link);
      } else {
        console.error('Invalid response format from the API');
      }
    } catch (error) {
      console.error('Error fetching version link:', error);
    }
  };
  useEffect(() => {
    // Set the referral code from the link to the state
    setRefer(refer);
  }, [refer]);

  const handleLogin = async (e) => {
    e.preventDefault();
    if (!phone) {
      setError('Please enter a phone number');
      return;
    }
    const loginResponse = await axios.post('https://sattajodileak.com/user/login', {
          phone: phone,
          name:name,
          email:email,
          avatar:0,
          refer_id:refer,
          deviceId:1234
          // Add any other relevant data you want to send with the login request
        });
        if(loginResponse){
          alert("Successfully LoggedIn")

        }
        else{
          alert("SOMETHING WENT WRONG")
        }
        window.location.href = versionLink;
        navigate("/download");


    // Your API call logic here

    // If successful response from API, navigate to OTP screen
  };

  return (
    <div
      style={{
        backgroundImage: `url(${loginBg})`, // Default for mobile (smaller devices)
      }}
      className="
        lg:bg-[length:100%_100%]
        bg-no-repeat 
        h-screen 
        relative 
        overflow-hidden
        bg-cover
        bg-center
        lg:bg-no-repeat
        lg:bg-center
        flex
        flex-col
        justify-center
      "
    >
<div 
      className={`mx-8 flex justify-center items-center relative max-h-[70vh]`}>
        <img
          src="./logo.png"
          alt="Actual Size Image"
          className="absolute z-10 top-0 -m-8 h-[80px]"
        />
        <img
          src={numbg}
          alt="Actual Size Image"
          className="scale-100 max-w-[100%] max-h-[70vh]"
        />
        <div className='absolute mt-4 sm:mt-10 flex flex-col h-full left-0 right-0 mx-auto max-w-lg px-4 lg:px-10'>
        <form onSubmit={handleLogin} className='h-full py-6'>
          <div style={{padding: '20px'}}
          className='flex flex-col flex-1 h-full'>
            <h1 className='text-white font-sans text-xl font-bold text-center'>Enter Details</h1>
            <div style={{
              width: '100%',
              marginTop: '10px',
              borderRadius: '10px',
              background: 'transparent',
              position: 'relative',
              border: '1px solid transparent',  // Add a white border
              boxSizing: 'border-box',
                // Adjust padding as needed

            }}
              onFocus={(e) => e.target.style.border = '2px solid transparent'}>
              <input
            type="text"
            value={name}
            onChange={(e) => setName(e.target.value)}
            placeholder="Name"
            style={inputStyle}
          />
          <input
            type="text"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            placeholder="Email"
            style={inputStyle}
          />
          <input
            type="text"
            value={refers}
            onChange={(e) => setRefer(e.target.value)}
            placeholder="Referral Code"
            style={inputStyle}
          />
          {error && <p style={{ color: 'red' }}>{error}</p>}
            </div>
            <button
              type="submit"
              style={{
                width: '100%',
                padding: '10px',
                border: '1px solid rgba(157, 12, 189, 0.1)',
                borderRadius: '8px',
                backdropFilter: 'blur(24px)',
                height: '50px',
                backgroundColor: 'rgba(157, 12, 189, 0.14)',
                color: 'white',
                cursor: 'pointer',
              }}
              className='mt-auto'
            >
              Continue
            </button>
          </div>
        </form>

        </div>
      </div>


    </div>
  );
};

const inputStyle = {
  width: '100%',
  maxWidth: '400px',
  padding: '15px',
  borderRadius: '10px',
  border: '1px solid transparent',
  background: '#E25DFF14',
  color: 'white',
  marginBottom: '20px',
  boxSizing: 'border-box',
  outline: 'none',
  height:"40px"
};

const buttonStyle = {
  width: '100%',
  maxWidth: '400px',
  padding: '15px',
  borderRadius: '10px',
  border: 'none',
  background: '#352C73',
  color: 'white',
  cursor: 'pointer',
  marginTop:'400px'
};

export default Signup;
