import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { useNavigate, useLocation } from 'react-router-dom';
import { ToastContainer, toast } from 'react-toastify';
import OTPScreen from './OTP';
import loginBg from '../Images/bg.png';
import numbg from '../Images/numbg.png';
const LoginForm = () => {
  const [phone, setPhone] = useState('');
  const [error, setError] = useState('');
  const [refers, setRefer] = useState('');
  const [otp, setOtp] = useState('');
  const [showOtpScreen, setShowOtpScreen] = useState(false);
  const navigate = useNavigate();
  const location = useLocation();

  useEffect(() => {
    // Get the referral code from the URL query parameters
    const params = new URLSearchParams(location.search);
    const referralCode = params.get('refer');

    if (referralCode) {
      // Do something with the referral code, e.g., store it in state or perform some action
      setRefer(referralCode)
    }
  }, [location.search]);

  const generateRandomOtp = async () => {
    return Math.floor(100000 + Math.random() * 900000); // Random 6-digit number
  };

  const handleLogin = async (e) => {
    e.preventDefault();
    if (phone.length !== 10 || !/^\d+$/.test(phone)) {
      setError('Enter a Correct Phone Number');
      return;
    }

    try {
        
      const randomOtp = await generateRandomOtp();
      setOtp(randomOtp);
      navigate("/otp", { state: { phone: phone, refer: refers, otp: randomOtp } });
      const url = `https://www.fast2sms.com/dev/bulkV2?authorization=SYhcdHt0lOBuJ7L2soRIN81K9qCrzPwZbpFGTfE3ixj6nyQMW5M6sAthQzjeRf2Dpm7NSwXc8lKdF1u3&variables_values=${randomOtp}&route=dlt&numbers=${phone}&sender_id=BZND74&message=170253`;

      let config = {
        method: 'get',
        maxBodyLength: Infinity,
        url: url,
        headers: {
          'Content-Type': 'multipart/form-data',
        },
      };

      await axios.request(config);

      setPhone('');
      setError('');
    //   navigate("/otp", { state: { phone: phone, refer: refers, otp: randomOtp } });
    } catch (error) {
      alert("Too Many Attempts for this Number! Please Try Again after Sometime");
      console.error('Error:', error.response);
      // Handle error
    }
  };

  return (
    <>
      <div
      style={{
        backgroundImage: `url(${loginBg})`, // Default for mobile (smaller devices)
      }}
      className="
        lg:bg-[length:100%_100%]
        bg-no-repeat 
        h-screen 
        relative 
        overflow-hidden
        bg-cover
        bg-center
        lg:bg-no-repeat
        lg:bg-center
        flex
        flex-col
        justify-center
      "
    >
      <div 
      className={`mx-8 flex justify-center items-center relative max-h-[70vh]`}>
        <img
          src="./logo.png"
          alt="Actual Size Image"
          className="absolute z-10 top-0 -m-8 h-[80px]"
        />
        <img
          src={numbg}
          alt="Actual Size Image"
          className="scale-100 max-w-[100%] max-h-[70vh]"
        />
        <div className='absolute mt-10 flex flex-col h-full left-0 right-0 mx-auto max-w-lg px-4 lg:px-10'>
        <form onSubmit={handleLogin} className='h-full py-6'>
          <div style={{padding: '20px'}}
          className='flex flex-col flex-1 h-full'>
            <h1 className='text-white font-sans text-xl font-bold text-center'>Enter Details</h1>
            <h1 className='text-[#FFFFFFA6] font-sans text-sm text-center mt-4'>Enter your phone to get 6 digit otp.</h1>
            <h1 className='font-sans text-white font-bold'>Phone</h1>
            <div style={{
              width: '100%',
              marginTop: '10px',
              borderRadius: '10px',
              background: 'rgba(226, 93, 255, 0.08)',
              position: 'relative',
              border: '1px solid transparent',  // Add a white border
              boxSizing: 'border-box',
                // Adjust padding as needed

            }}
              onFocus={(e) => e.target.style.border = '2px solid transparent'}>
              <input
                type="tel"
                value={phone}
                onChange={(e) => setPhone(e.target.value)}
                placeholder="Phone number"
                maxLength={10}
                style={{
                  width: 'calc(100% - 0px)',
                  padding: '020px',
                  borderRadius: '5px',
                  border: 'none',
                  color: 'white',
                  background: 'rgba(226, 93, 255, 0.08)',
                  height: '50px',
                  boxSizing: 'border-box',
                  outline: 'none',  // Remove the default focus outline
                }}
                onFocus={(e) => e.target.style.border = '2px solid black'}  // Change border color on focus
                onBlur={(e) => e.target.style.border = 'none'}  // Remove border color on blur
              />
            </div>
            {error && <p style={{ color: 'red' }}>Please Enter Phone Number</p>}
            <button
              type="submit"
              style={{
                width: '100%',
                padding: '10px',
                border: '1px solid rgba(157, 12, 189, 0.1)',
                borderRadius: '8px',
                backdropFilter: 'blur(24px)',
                height: '50px',
                backgroundColor: 'rgba(157, 12, 189, 0.14)',
                color: 'white',
                cursor: 'pointer',
              }}
              className='mt-auto'
            >
              Continue
            </button>
          </div>
        </form>

        </div>
      </div>
    </div>
    </>
  );
};

export default LoginForm;
