import React from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';
import { Navigation, Pagination, Autoplay } from 'swiper/modules';
import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/pagination';

// Images
import snakeLadders from '../Images/fruit_ninja_img.png';
import fruitNinja from '../Images/dragonTiger.png';
import chess from '../Images/bingoimg.png';
import spin from '../Images/Lucky Spin.png';
import mines from '../Images/mines_img.png';
import plinko from '../Images/plinko_img.png';
import slice from '../Images/slice_master.png';

const GameSwiper = () => {
  const games = [
    { src: snakeLadders, title: 'Snake & Ladders' },
    { src: fruitNinja, title: 'Fruit Ninja' },
    { src: chess, title: 'Chess' },
    { src: spin, title: 'Lucky Spin' },
    { src: mines, title: 'Mines' },
    { src: plinko, title: 'Plinko' },
    { src: slice, title: 'Slice Master' },
  ];

  return (
    <div className="w-full mx-auto px-4 sm:px-6 lg:px-8 mt-6">
      <h2 className="text-center text-white text-2xl sm:text-3xl font-bold mb-6">
        Our Popular Games
      </h2>
      <Swiper
        modules={[Navigation, Pagination, Autoplay]}
        spaceBetween={20}
        slidesPerView={1} // Default for smallest screens
        breakpoints={{
          640: { slidesPerView: 2 },
          768: { slidesPerView: 2 },
          1024: { slidesPerView: 3 },
          1280: { slidesPerView: 4 },
        }}
        autoplay={{ delay: 2000 }}
        // navigation
        // pagination={{ clickable: true }}
      >
        {games.map((game, index) => (
          <SwiperSlide key={index}>
            <div className=" p-4 rounded-xl shadow-lg hover:shadow-2xl transition-shadow duration-300 ease-in-out items-center justify-center flex">
              <img
                src={game.src}
                alt={game.title}
                className="h-[350px] sm:h-[350px] object-cover rounded-lg"
              />
              
            </div>
          </SwiperSlide>
        ))}
      </Swiper>
    </div>
  );
};

export default GameSwiper;
