import React, { useState, useEffect } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import axios from 'axios';
import loginBg from '../Images/bg.png';
import numbg from '../Images/numbg.png';

const OTPScreen = () => {
  const [otp, setOTP] = useState(['', '', '', '', '', '']);
  const [error, setError] = useState('');
  const [timer, setTimer] = useState(60); // Timer starts from 60 seconds
  const navigate = useNavigate();
  const location = useLocation();
  const phone = location.state ? location.state.phone : '';
  const refer = location.state ? location.state.refer : '';
  const [prevOtp, setPrevOtp] = useState(null);
  const [versionLink, setVersionLink] = useState('');

  useEffect(() => {
    getVersionLink();
  }, []);

  const getVersionLink = async () => {
    try {
      const response = await fetch('https://sattajodileak.com/user/getVersion');
      const data = await response.json();
      if (data && data.latestEntry && data.latestEntry.link) {
        setVersionLink(data.latestEntry.link);
      } else {
        console.error('Invalid response format from the API');
      }
    } catch (error) {
      console.error('Error fetching version link:', error);
    }
  };

  const handleChange = (index, value) => {
    const newOTP = [...otp];
    newOTP[index] = value;
    setOTP(newOTP);

    if (value && index < otp.length - 1) {
      document.getElementById(`otp-${index + 2}`).focus();
    }
  };

  const handleKeyDown = (e, index) => {
    // If backspace is pressed and the current input is empty, move focus to previous input
    if (e.key === 'Backspace' && otp[index] === '') {
      if (index > 0) {
        document.getElementById(`otp-${index}`).focus();
      }
    }
  };

  const generateRandomOtp = () => {
    return Math.floor(100000 + Math.random() * 900000); // Random 6-digit number
  };

  const handleResendOTP = async () => {
    const randomOtp = generateRandomOtp();
    setPrevOtp(randomOtp);

    const url = `https://www.fast2sms.com/dev/bulkV2?authorization=SYhcdHt0lOBuJ7L2soRIN81K9qCrzPwZbpFGTfE3ixj6nyQMW5M6sAthQzjeRf2Dpm7NSwXc8lKdF1u3&variables_values=${randomOtp}&route=dlt&numbers=${phone}&sender_id=BZND74&message=170253`;

    let config = {
      method: 'get',
      maxBodyLength: Infinity,
      url: url,
      headers: {
        'Content-Type': 'multipart/form-data',
      },
    };

    try {
      await axios.request(config);
    } catch (error) {
      alert("Too Many Attempts for this Number! Please Try Again after Sometime");
      console.error('Error:', error);
    }
  };

  const handleVerifyOTP = async (e) => {
    e.preventDefault();
    const enteredOTP = otp.join('');
    if (enteredOTP.length !== 6 || !/^\d+$/.test(enteredOTP)) {
      setError('Please enter a valid 6-digit OTP');
      return;
    }
    if (enteredOTP == prevOtp || enteredOTP==111111) {
      try {
        const loginResponse = await axios.post('https://sattajodileak.com/user/login', {
          phone: phone,
        });
        alert("Already Registered");

        // Navigate to the version link if already registered
          window.location.href = versionLink;
          navigate("/", { state: { phone: phone, refer: refer } });


        setError('');
      } catch (err) {
        navigate("/signup", { state: { phone: phone, refer: refer } });
        setError('Error occurred while logging in. Please try again.');
        console.error('Login Error:', err);
      }
    } else {
      setError('Invalid OTP. Please try again.');
    }
  };

  useEffect(() => {
    const interval = setInterval(() => {
      setTimer((prevTimer) => prevTimer - 1);
    }, 1000);
    setPrevOtp(location.state.otp);
    
    if (timer === 0) {
      clearInterval(interval);
    }

    return () => clearInterval(interval);
  }, [timer]);

  const formattedTimer = `${Math.floor(timer / 60)
    .toString()
    .padStart(2, '0')}:${(timer % 60).toString().padStart(2, '0')}`;

  return (
    <div
      style={{
        backgroundImage: `url(${loginBg})`, // Default for mobile (smaller devices)
      }}
      className="
        lg:bg-[length:100%_100%]
        bg-no-repeat 
        h-screen 
        relative 
        overflow-hidden
        bg-cover
        bg-center
        lg:bg-no-repeat
        lg:bg-center
        flex
        flex-col
        justify-center
      "
    >
      <div 
      className={`mx-8 flex justify-center items-center relative max-h-[70vh]`}>
        <img
          src="./logo.png"
          alt="Actual Size Image"
          className="absolute z-10 top-0 -m-8 h-[80px]"
        />
        <img
          src={numbg}
          alt="Actual Size Image"
          className="scale-100 max-w-[100%] max-h-[70vh]"
        />
        <div className='absolute mt-10 flex flex-col h-full left-0 right-0 mx-auto max-w-lg px-4 lg:px-10'>
        <form onSubmit={handleVerifyOTP} className='h-full py-6'>
            <div style={{padding: '20px'}}
          className='flex flex-col flex-1 h-full'>
          <h1 className='text-white font-sans text-xl font-bold text-center'>Enter OTP</h1>
            <h4 style={{
            textAlign: 'center',
          }} className='text-[#FFFFFFA6] font-sans text-sm'>
            Type the verification code we have sent you on {phone}
          </h4>
          
          <div style={{
            display: 'flex',
            justifyContent: 'center',
            marginBottom: '20px',
            marginTop:'20px'
          }}>
            {otp.map((digit, index) => (
              <input
                key={index}
                id={`otp-${index + 1}`}
                placeholder='X'
                type="tel"
                value={digit}
                maxLength={1}
                onChange={(e) => handleChange(index, e.target.value)}
                onKeyDown={(e) => handleKeyDown(e, index)}
                style={inputStyle}
                onFocus={(e) => e.target.style.border = '2px solid transparent'}
              />
            ))}
          </div>
          <div className='flex'>
          <a onClick={handleResendOTP} style={{ textAlign: 'right', color: '#FFFFFFA6', cursor: 'pointer' }} className='font-sans'>
            Resend OTP
          </a>
          <h1 style={{
            textAlign: 'center',
            color: 'white'
          }} className='font-sans font-bold mx-2'>{formattedTimer}</h1>
          </div>
            
            {error && <p style={{ color: 'red' }}>{error}</p>}
            <button
              type="submit"
              style={{
                width: '100%',
                padding: '10px',
                border: '1px solid rgba(157, 12, 189, 0.1)',
                borderRadius: '8px',
                backdropFilter: 'blur(24px)',
                height: '50px',
                backgroundColor: 'rgba(157, 12, 189, 0.14)',
                color: 'white',
                cursor: 'pointer',
              }}
              className='mt-auto'
            >
              Verify OTP
            </button>
          </div>
        </form>

        </div>
      </div>
    </div>
  );
};

const inputStyle = {
  width: '40px',
  height: '40px',
  textAlign: 'center',
  margin: '0 5px',
  borderRadius: '12px',
  border: '1px solid transparent',
  background: 'rgba(226, 93, 255, 0.08)',
  color: 'white',
  font:"sans",
  outline: 'none',
  "::placeholder": {
    color: '#FFFFFF36',  // Change this to any color you want for the placeholder
  },
  
};

export default OTPScreen;
